import { _decorator, Component, Node,find,instantiate, EventTouch, math,Sprite } from 'cc';
const { ccclass, property } = _decorator;
import { common_getEventTarget ,EventEnum_Scene} from './Common';

@ccclass('gameD_ColorButton')
export class gameD_ColorButton extends Component {
    
    private blTouchSW:boolean = false;//false: true:
    private btn_mask:Node = null;
    private blueNode:Node = null;
    private yellowNode:Node = null;

    public iColor:number = 0; //0=blue 1=yallow

    private last_time:number = 0;
    private switch_fill:number = 0;
    private blColorSwitch:boolean = false;


    start() {
        this.blTouchSW = false;
        this.btn_mask = this.node.getChildByName("Mask");
        this.blueNode = this.node.getChildByName("Blue");
        this.yellowNode = this.node.getChildByName("Yellow");

        this.set_iColor(0);
        this.btn_mask.on(Node.EventType.TOUCH_START, this.onTouchStart,this);


        //this.blTouchSW = true;//test
    }
    //
    public touchSwitch(sw:boolean){
        this.blTouchSW = sw;
    }

    // 0=blue 1=yallow
    public set_iColor(_iColor:number){
        this.iColor = _iColor;
        this.blueNode.active = true;
        this.yellowNode.active = true;
        let y_sprite:Sprite = this.yellowNode.getComponent(Sprite);
        
        if(this.iColor == 0){
            y_sprite.fillRange = 0;
        }
        else{
            y_sprite.fillRange = 1;
        }
    }

    public switchColor(){
        this.switch_fill = 0;
        this.last_time = Date.now();
        this.blColorSwitch = true;

    }
       
    onTouchStart(event: EventTouch) {
        if(this.blTouchSW==false) return;
        if(this.blColorSwitch == true) return;

        this.switchColor();

        
    }

    update(deltaTime: number) {
        if(this.blColorSwitch == true){
            let now_time:number = Date.now();

            if((Date.now()-this.last_time) >= 33){
                this.last_time = Date.now();

                this.switch_fill += 0.2;

        
                let y_sprite:Sprite = this.yellowNode.getComponent(Sprite);
                

           
                if(this.switch_fill >= 1){
                    this.blColorSwitch = false;  
                    this.set_iColor(this.iColor==0?1:0);
                }
                else{
                    if(this.iColor == 0){
                        y_sprite.fillRange = 0-this.switch_fill;
                    }
                    else{
                        y_sprite.fillRange = 1-this.switch_fill;
                    }
                }
            }
        }
        
        
    }
}

