import { find, UITransform,Node,RichText, Sprite, Color, log, EventTouch,Label, random, instantiate} from 'cc';
import { common_getEventTarget,EventEnum_Scene,common_bind_touch_start,getST,CState,CFSM } from './Common';
import { AudioManager } from './AudioManager';
import { SpriteFrameManager } from './SpriteFrameManager';
import { gameD_ColorButton } from './gameD_ColorButton';
import { gameD_DiamondBox } from './gameD_DiamondBox';
import { gtool_start_time } from './gtool_start_time';


class CBoxData{
    //
    public iRL:number = 0;//0=right 1=left
    public iUB:number = 0;//0=UP 1=DOWN
    
    public iColor:number = 0;//0=blue 1=yallow

    public blMoveFast:boolean = false;//

    public iBoxID:number = 0;
    public iBoxNode:Node = null;
    public diamondBox:gameD_DiamondBox = null;

}

class CBoxTool{
    public cBoxs:CBoxData[] = null;

    public iBoxID_G:number = 0;
    
    private next_createBox_wait_time:number = 0;//
    private createBox_wait_time_last:number = 0;//

    private iMinTime:number = 200;//
    private iMxnTime:number = 500;//
    private iMinLineTime:number = 1000;//
    private last_RL_create_time:number[] = [0,0];

    private iMaxFastBox:number = 1;//
    private iMaxSlowBox:number = 2;//
    

    private base_box_node:Node = null;//
    private clone_parent_node:Node = null;//

    public init(_box_base_node:Node,_clone_parent_node:Node){
        this.iBoxID_G = 0;
        this.cBoxs = [];
        this.next_createBox_wait_time = this.getNextCreateBoxWaitTime();
        this.createBox_wait_time_last = Date.now();
        this.base_box_node = _box_base_node;
        this.clone_parent_node = _clone_parent_node;
    }

    public createNewBox():boolean{
        let iMaxBox = this.iMaxFastBox + this.iMaxSlowBox;
        if(this.cBoxs.length >= iMaxBox){
            return false;
        }
        if((Date.now() - this.createBox_wait_time_last) >= this.next_createBox_wait_time){
            let _iRL = Math.floor(Math.random() * 2);
            if(Date.now() - this.last_RL_create_time[_iRL] > this.iMinLineTime){
                this.last_RL_create_time[_iRL] = Date.now();
                this.createBox_wait_time_last = Date.now();
                this.next_createBox_wait_time = this.getNextCreateBoxWaitTime();
                let _cBox = new CBoxData();
                this.iBoxID_G++;
                _cBox.iBoxID = this.iBoxID_G;
                _cBox.iColor = Math.floor(Math.random() * 2);
                _cBox.iRL = _iRL;
                _cBox.iUB = Math.floor(Math.random() * 2);
                let imoveType:boolean[] = [];
                let _cnt_f:number = 0;
                let _cnt_s:number = 0;
                for(let i = 0;i < this.cBoxs.length;i++){
                    if(this.cBoxs[i].blMoveFast){
                        _cnt_f++;
                    }else{
                        _cnt_s++;
                    }
                }
                _cnt_f = this.iMaxFastBox - _cnt_f;
                _cnt_s = this.iMaxSlowBox - _cnt_s;
                for(let i = 0;i < _cnt_f;i++){
                    imoveType.push(true);
                }
                for(let i = 0;i < _cnt_s;i++){
                    imoveType.push(false);
                }

                _cBox.blMoveFast = imoveType[Math.floor(Math.random() * imoveType.length)];
                log("blMoveFast",_cBox.blMoveFast);

                _cBox.iBoxNode = instantiate(this.base_box_node);
                _cBox.diamondBox = _cBox.iBoxNode.getComponent(gameD_DiamondBox);
                _cBox.diamondBox.init(_cBox.iBoxID,_cBox.iColor,_cBox.iRL,_cBox.iUB,_cBox.blMoveFast);
                this.clone_parent_node.addChild(_cBox.iBoxNode);
                this.cBoxs.push(_cBox);
                return true;
            }
        }
        return false;
    }

    public delBox(_BoxID:number){
        for(let i = 0;i<this.cBoxs.length;i++){
            if(this.cBoxs[i].iBoxID == _BoxID){
                this.clone_parent_node.removeChild(this.cBoxs[i].iBoxNode);
                this.cBoxs[i].iBoxNode.destroy();
                this.cBoxs.splice(i,1);
                this.createBox_wait_time_last = Date.now();
                return;
            }
        }
    }

    public deleteAll(){
        this.clone_parent_node.removeAllChildren();
        this.cBoxs = [];
    }


    private getNextCreateBoxWaitTime():number{
        let _iTime = Math.random() * (this.iMxnTime - this.iMinTime) + this.iMinTime;
        return Math.floor(_iTime);
    }

}

//
enum D_scene_state  { 
    st_none=100,    //
    st_init,        // 
    st_tutorial,    //
    st_into_animation, //
    st_play_input, //
    st_timeup_animation, //
    st_show_score, //
    st_end, //
}

class Proc_D_scene{ 

    
    private last_timestamp:number = 0;//FPS
    private cFSM:CFSM = new CFSM();

    private md:any = null;
    private mission:number = 0;//
    private right_mission:number = 0;//
    private error_mission:number = 0;//
    private ComboCount:number = 0;//
    private last_is_right:boolean = false;//

    private play_last_time:number = 0;//

    private node_StartTime:Node = null;//
    private node_TimesUp:Node = null;//
    private node_score_panle:Node = null;//
    private colorButton_RLs:gameD_ColorButton[] = [];

    private box_base_node:Node = null!;
    private clone_node:Node = null!;
    private cBoxTool:CBoxTool = null;

    private strTutorialPath:string[] = [
        "Canvas/tutorial/_01",
        "Canvas/tutorial/_02",
        "Canvas/tutorial/_03"
    ];
    private blPlay10TimesSound:boolean = false;


    
    public init(){

        //
        this.cFSM = new CFSM();
        this.add_FSM_state();

        this.cFSM.set_Next_state(D_scene_state.st_init);
        this.last_timestamp = Date.now();


        this.md = getST().get_mission_data();
        this.mission = 0;
        this.right_mission = 0;
        this.ComboCount = 0;
        this.last_is_right = false;

       
        find("Canvas/").once(Node.EventType.TOUCH_START, function(){
            AudioManager.playSound("UI_click");
            common_getEventTarget().emit(EventEnum_Scene.Go_GameHome);
        }, this);

        find("Canvas/tutorial").active = false;
        this.node_StartTime = find("Canvas/start");
        this.node_StartTime.active = false;
        this.node_TimesUp = find("Canvas/timesup");
        this.node_TimesUp.active = false;
        this.node_score_panle = find("Canvas/score_panle");
        this.node_score_panle.active = false;

        this.box_base_node = find("Canvas/box/base/diamond_box");
        this.clone_node = find("Canvas/box/clone");

        this.colorButton_RLs.push(find("Canvas/button_right").getComponent(gameD_ColorButton));
        this.colorButton_RLs.push(find("Canvas/button_left").getComponent(gameD_ColorButton));

        this.cBoxTool = new CBoxTool();
        this.cBoxTool.init(this.box_base_node,this.clone_node);

        
        common_getEventTarget().on(EventEnum_Scene.GameD_event_BoxMoveEnd, this.gameD_event_BoxMoveEnd, this);
    }
    private add_FSM_state(){
        this.cFSM.add_new_state(D_scene_state.st_init,"st_init",this.st_init_into,this.st_init_update,this);
        this.cFSM.add_new_state(D_scene_state.st_tutorial,"st_tutorial",this.st_tutorial_into,this.st_tutorial_update,this);
        this.cFSM.add_new_state(D_scene_state.st_into_animation,"st_into_animation",this.st_into_animation_into,this.st_into_animation_update,this);
        this.cFSM.add_new_state(D_scene_state.st_play_input,"st_play_input",this.st_play_input_into,this.st_play_input_update,this);
        this.cFSM.add_new_state(D_scene_state.st_timeup_animation,"st_timeup_animation",this.st_timeup_animation_into,this.st_timeup_animation_update,this);
        this.cFSM.add_new_state(D_scene_state.st_show_score,"st_show_score",this.st_show_score_into,this.st_show_score_update,this);
        this.cFSM.add_new_state(D_scene_state.st_end,"st_end",this.st_end_into,this.st_end_update,this);
    }
       

    private gameD_event_BoxMoveEnd(iType:number,_BoxID:number){
        if(this.cFSM.get_now_state().state != D_scene_state.st_play_input) return;
        //log(iType,_BoxID);
        if(iType == 0){
            this.right_mission++;
            AudioManager.playSound("answer02");
            if(this.last_is_right){
                this.ComboCount++;
            }
            this.last_is_right = true;

        }else if(iType == 1){
            this.error_mission++;
            AudioManager.playSound("answer01");
            this.last_is_right = false;
        }
        this.mission++;

        if(iType == 2){
            this.cBoxTool.delBox(_BoxID);
        }
    }


    public st_init_into(){
       
        this.mission = 0;//
        this.right_mission = 0;//
        this.error_mission = 0;//
        this.ComboCount = 0;
        this.last_is_right = false;

        this.play_last_time = 0;//
        this.node_score_panle.active = false;

        this.cBoxTool.deleteAll();
        this.cFSM.set_Next_state(D_scene_state.st_tutorial);
        this.blPlay10TimesSound = false;
    }

    public st_init_update(){}
    public st_tutorial_into(){
        find("Canvas/tutorial").active = true;
        let btn_start = find("Canvas/tutorial/btn_start");
        let btn_next_page = find("Canvas/tutorial/btn_next_page");
        btn_start.once(Node.EventType.TOUCH_START,
            function(){
                this.cFSM.set_Next_state(D_scene_state.st_into_animation);
                find("Canvas/tutorial").active = false;
                this.node_StartTime.getComponent(gtool_start_time).switchShow(true);
                AudioManager.playSound("countdown01");
            }, this);

        btn_start.active = true;
        btn_next_page.active = true;

        btn_next_page.off(Node.EventType.TOUCH_START);
        btn_next_page.on(Node.EventType.TOUCH_START,
            function(){
                let _page:number = 0;
                for(let i=0;i<this.strTutorialPath.length;i++){
                    if(find(this.strTutorialPath[i]).active == true){
                        _page = i;
                        break;
                    }
                }
                _page++;
                if(_page >= this.strTutorialPath.length){
                    _page = 0;
                }
                
               log("page = "+_page);
                for(let i=0;i<this.strTutorialPath.length;i++){
                    find(this.strTutorialPath[i]).active = false;
                    if(i == _page){
                        find(this.strTutorialPath[i]).active = true;
                    }
                }
            }, this);

        for(let i=0;i<this.strTutorialPath.length;i++){
            find(this.strTutorialPath[i]).active = false;
        }
        find(this.strTutorialPath[0]).active = true;
        
    }
    public st_tutorial_update(){}
    public st_into_animation_into(){
        AudioManager.stopMusic();
    }
    public st_into_animation_update(){
        if(this.node_StartTime.getComponent(gtool_start_time).blTimesUP){
            this.node_StartTime.active = false;
            this.cFSM.set_Next_state(D_scene_state.st_play_input);
            this.play_last_time = this.md.Time*1000;

            AudioManager.playMusic("bgm02");
        }
    }
    public st_play_input_into(){
        for(let i=0;i<this.colorButton_RLs.length;i++){
            this.colorButton_RLs[i].touchSwitch(true);
        }
    }
    public st_play_input_update(){
        if(this.cBoxTool.createNewBox()){
            this.mission++;
        }
    }
    public st_timeup_animation_into(){
        this.node_TimesUp.getComponent(gtool_start_time).switchShow(true);
        AudioManager.stopMusic();
    }
    public st_timeup_animation_update(){
        if(this.node_TimesUp.getComponent(gtool_start_time).blTimesUP){
            this.cFSM.set_Next_state(D_scene_state.st_show_score);
        }
    }
    public st_show_score_into(){
        this.mission = this.right_mission+this.error_mission;
        getST().setWin(this.mission,this.right_mission,this.ComboCount);
        this.node_score_panle.active = true;
        this.node_TimesUp.active = false;

        this.node_score_panle.getChildByName("again").once(Node.EventType.TOUCH_START,
             function(){
                AudioManager.playSound("UI_click");
                this.cFSM.set_Next_state(D_scene_state.st_init);
             }, 
             this);
        this.node_score_panle.getChildByName("exit").once(Node.EventType.TOUCH_START,
            function(){
                AudioManager.playSound("UI_click");
                this.cFSM.set_Next_state(D_scene_state.st_end);
            }, 
            this);
        
        this.node_score_panle.getChildByName("right_num").getComponent(Label).string = this.right_mission.toString();
        this.node_score_panle.getChildByName("err_num").getComponent(Label).string = this.error_mission.toString();
        this.node_score_panle.getChildByName("rate").getComponent(Label).string = Math.floor(this.right_mission*100/this.mission).toString()+"%";

        this.node_score_panle.getChildByName("time_num").getComponent(Label).string = getST().time_num.toString()+'s';
        this.node_score_panle.getChildByPath("score/right_score").getComponent(RichText).string = "<b><color=#7a4706>"+getST().play_score.toString()+"</color></b>";
        this.node_score_panle.getChildByPath("score/combo_score").getComponent(RichText).string = "<b><color=#7a4706>"+getST().comble_score.toString()+"</color></b>";
        this.node_score_panle.getChildByPath("score/total_score").getComponent(RichText).string = "<b><color=#7a4706>"+getST().play_totle_score.toString()+"</color></b>";
        let bastScore = getST().myRankData[getST().select_Game-1].score;
        this.node_score_panle.getChildByPath("score/total_score-001").getComponent(RichText).string = "<b><color=f8e0b3>"+bastScore.toString()+"</color></b>";
        let strTitlePath:string[] = [ //0    1	  2	  3	  4	
            "txt_objext/title/__",
            "txt_objext/title/__",
            "txt_objext/title/__",
            "",
            "txt_objext/title/__",
        ]
        for(let i=0;i<5;i++){
            if(i == 3)
                continue;
            if(i == (getST().select_Game-1)){
                this.node_score_panle.getChildByPath(strTitlePath[i]).active = true;
            }
            else{
                this.node_score_panle.getChildByPath(strTitlePath[i]).active = false;
            }

        }
        AudioManager.playSound("result01");
    }
    public st_show_score_update(){
        let step:number = Math.floor(this.cFSM.get_now_state().elapsed_time/33);
        if(step <= 25){
            let rate:number =  this.right_mission/this.mission;
            let rateP:number = rate*(step/25);
            rateP = (Math.round(rateP * 100) / 100)*-1;
            find("Canvas/score_panle/_").getComponent(Sprite).fillRange = rateP;
        }
    }
    public st_end_into(){
        common_getEventTarget().emit(EventEnum_Scene.Go_GameHome);
    }
    public st_end_update(){}


        



   
    
   
    

    public update(){
        //
        let elapsed_time = Date.now() - this.last_timestamp;
        
        if(elapsed_time < 33){
            return;
        }
        this.last_timestamp = Date.now();
        this.cFSM.update();

        
        if(this.cFSM.get_now_state().state == D_scene_state.st_play_input){
            //------------------------
            if(getST().getIsInTestMode()){
                elapsed_time = 0;   
            }
            //---------------------------------
            this.play_last_time = this.play_last_time-elapsed_time;
            if(this.play_last_time < 0){
                this.play_last_time = 0;
            }
            let _sec = Math.floor(this.play_last_time/1000).toString().padStart(2, '0');
            let _msec = Math.floor(this.play_last_time%1000/10).toString().padStart(2, '0');
        
            find("Canvas/time/TimeText_sec").getComponent(RichText).string = "<color=#ffffff>"+_sec+":"+ "</color>";
            find("Canvas/time/TimeText_msec").getComponent(RichText).string = "<color=#ffffff>"+_msec+ "</color>";
            if(this.play_last_time<=0){
                for(let i=0;i<this.colorButton_RLs.length;i++){
                    this.colorButton_RLs[i].touchSwitch(false);
                }
                this.cFSM.set_Next_state(D_scene_state.st_timeup_animation);
            }
            if(this.blPlay10TimesSound==false){
                if(this.play_last_time <= 3000){
                    this.blPlay10TimesSound = true;
                    AudioManager.playSound("countdown01");
                }
            }
        }
    }
    
}



var _proc_D_scene:Proc_D_scene = null;


export function getProc_D_scene() {

    if(_proc_D_scene === null) {
        _proc_D_scene = new Proc_D_scene();
        console.log('new Proc_D_scene');
    }
    return _proc_D_scene;
} 

