export class CState{
    public state:number = 0;
    public state_name:string = "";
    public into_func:Function = null;
    public update_func:Function = null;
    public out_func:Function = null;
    public wait_time:number = -1;
    public elapsed_time:number = 0;//update
    public last_into_time:number = 0;//


    public skip_to_state:number = -1;// -1

    constructor(_state:number,_state_name:string,_into_func:Function,_update_func:Function,target:any){
        this.state = _state;
        this.state_name = _state_name;
        this.into_func = _into_func.bind(target);
        this.update_func = _update_func.bind(target);
    }
}

export class CFSM{
    private state_map: Record<number, CState> = {};
    private state:number = 0
    private state_next:number = -1;//-1

    private last_time:number = 0;


    public add_new_state(_state:number,_state_name:string,_into_func:Function,_update_func:Function,target:any){
        let state = new CState(_state,_state_name,_into_func,_update_func,target);
        this.state_map[state.state] = state;    
    }

    public set_Next_state(_state_next:number):boolean{
        
        if(this.state_next == -1){
            let newCState:CState = this.state_map[_state_next];
            if(newCState){
                console.log("set_Next_state: "+newCState.state+"  "+newCState.state_name);
            
                this.state_next = _state_next;
                return true;   
            }
            return false;
        }
        return false;
    }

    public get_state(state:number):CState{
        return this.state_map[state];
    }

    public get_now_state():CState{
        return this.state_map[this.state];
    }

    public update(){

        this._state_change();
        this._state_update();
    }

    private _state_change(){
        if(this.state_next == -1){
            return;
        }

        let nowCState:CState = this.state_map[this.state];
        if(nowCState){
            if(nowCState.out_func != null){
                nowCState.out_func();
            }
        }
        let newCState:CState = this.state_map[this.state_next];

        this.state = this.state_next;
        this.state_next = -1;

        if(newCState.into_func != null){
            newCState.into_func();
            newCState.last_into_time = Date.now();
        }

        this.last_time = Date.now();
    }

    private _state_update(){
        let nowCState:CState = this.state_map[this.state];
        if(!nowCState){
            return;
        }
        
        if(nowCState.wait_time > 0){
            if(Date.now() - this.last_time < nowCState.wait_time){
                return;
            }
            this.last_time = Date.now();
        }
        nowCState.elapsed_time = Date.now() - nowCState.last_into_time;

        if(nowCState.update_func != null){
            nowCState.update_func();
        }
        if(nowCState.skip_to_state != -1){
            this.set_Next_state(nowCState.skip_to_state);
        }
    }
}