import { _decorator, Component, Node,find,instantiate, EventTouch, math } from 'cc';
const { ccclass, property } = _decorator;
import { common_getEventTarget ,EventEnum_Scene} from './Common';

@ccclass('gameB_MathButton')
export class gameB_MathButton extends Component {
    
    private blTouchSW:boolean = false;//false: true:
    private hide_last_time:number[] = [];

    start() {
        this.blTouchSW = false;
        for(let i=0;i<9;i++){
            this.node.children[i].on(Node.EventType.TOUCH_START, this.onTouchStart,this);
            this.hide_last_time.push(-1);
        }

    }
    //
    public touchSwitch(sw:boolean){
        this.blTouchSW = sw;
    }
       
    onTouchStart(event: EventTouch) {
        if(this.blTouchSW==false) return;
        
        common_getEventTarget().emit(EventEnum_Scene.GameB_event_MathButton,Number(event.target.name));
        for(let i=0;i<9;i++){
            if(this.node.children[i] == event.target){
                this.hide_last_time[i] = 3;
                this.node.children[i].active = false;
                break;
            }
        }
    }

    update(deltaTime: number) {
        for(let i=0;i<9;i++){
            if(this.hide_last_time[i] > 0){
                this.hide_last_time[i] -= deltaTime;
                
                if(this.hide_last_time[i] <= 0){
                    this.hide_last_time[i] = -1;
                    this.node.children[i].active = true;
                }
            }
        }
        
    }
}

