import { find, instantiate,Node,RichText, Sprite, Color,Label, EventTouch, log} from 'cc';
import { common_getEventTarget,EventEnum_Scene,common_bind_touch_start,getST,RankData } from './Common';
import { AudioManager } from './AudioManager';
import { SpriteFrameManager } from './SpriteFrameManager';





//
enum R_scene_state  { 
    st_none=100,    //
    st_init,        // 
    st_end, //
}

class Proc_R_scene{ 
    
    private state:number = R_scene_state.st_none;

   

    //node
    private page_name:string[] = [
        "page_01","page_02","page_03","page_04"
    ];

    private strButtonPath:string[] = [
        "Canvas/BG/view/__",
        "Canvas/BG/view/__",
        "Canvas/BG/view/__",
        "Canvas/BG//",
        "Canvas/BG/view/__"
    ] 

    private strButtonName:string[] = [
        "__",
        "__",
        "__",
        "",
        "__"
    ] 

    //
    private now_page:number = 1;


    
    
    public init(){
        
        this.now_page = 1;////1   2   3	  4	  5 
        
        common_bind_touch_start("Canvas/BG/_//",this.go_GameHome,this); 
        for(let i=0;i<this.strButtonPath.length;i++){
            common_bind_touch_start(this.strButtonPath[i],this.onSelectGame,this); 
        }
        common_bind_touch_start("Canvas/BG/view/",this.onCloseSelectGame,this); 
        common_bind_touch_start("Canvas/BG//",this.onOpenSelectGame,this); 

     
        this.update_page();
      
    }

    private onOpenSelectGame(){
        AudioManager.playSound("UI_click");
        let SelectGame:Node = find("Canvas/BG/view");
        SelectGame.active = true;
    }
    private onCloseSelectGame(){
        AudioManager.playSound("UI_click");
        let SelectGame:Node = find("Canvas/BG/view");
        SelectGame.active = false;
    }

    private onSelectGame(event:EventTouch){
        
        let SelectGame:Node = find("Canvas/BG/view");
        SelectGame.active = false;

        AudioManager.playSound("UI_click");

        let index:number = this.strButtonName.indexOf(event.target.name);
        if(index != -1){
            this.now_page = index+1;
            this.update_page();
        }
        //log("onSelectGame ",event.target.name," now_page ",this.now_page);
    }



    private update_page(){
        let clone:Node = find("Canvas/BG/ScrollView/view/content/clone");
        let baseItem:Node = find("Canvas/BG/ScrollView/view/content/base/item");

        clone.destroyAllChildren();
        clone.removeAllChildren();

        find("Canvas/BG/playerinfo/playerName").getComponent(RichText).string = "<b><color=#492c1c>"+getST().myRankData[this.now_page-1].name+"</color></b>";
        find("Canvas/BG/playerinfo/sc01").getComponent(RichText).string = "<b><color=#492c1c>"+getST().myRankData[this.now_page-1].rank+"</color></b>";
        find("Canvas/BG/playerinfo/sc02").getComponent(RichText).string = "<b><color=#492c1c>"+getST().myRankData[this.now_page-1].score+"</color></b>";
        
        let pageRankData:RankData[] = getST().rank_data[this.now_page-1];
        for(let i=0;i<pageRankData.length;i++){
            let item:Node = instantiate(baseItem);
            clone.addChild(item);

            let num:Label = item.getChildByName("num").getComponent(Label);
            let name:Label = item.getChildByName("name").getComponent(Label);
            let score:Label = item.getChildByName("score").getComponent(Label);
            num.string = pageRankData[i].rank.toString();
            name.string = pageRankData[i].name;
            score.string = pageRankData[i].score.toString();
            item.active = true;
            item.setPosition(0,(-65)*i);
        }

        let str_page_tip_path:string[] = [
            "Canvas/BG///___",
            "Canvas/BG///___",
            "Canvas/BG///___",
            "",
            "Canvas/BG///___",
        ];
        
        let ipos:number = this.now_page-1;
        if(ipos == 3) ipos = 0;
        for(let i=0;i<str_page_tip_path.length;i++){
            let page_tip:Node = find(str_page_tip_path[i]);
            if(i == ipos){
                page_tip.active = true;
            }
            else{
                page_tip.active = false;
            }
        }

        let str_page_title_path:string[] = [
            "Canvas/BG/_/_/__",
            "Canvas/BG/_/_/__",
            "Canvas/BG/_/_/__",
            "Canvas/BG/_/_/__",
            "Canvas/BG/_/_/__",
        ];
        ipos = this.now_page-1;
        for(let i=0;i<str_page_title_path.length;i++){
            let page_title:Node = find(str_page_title_path[i]);
            if(i == ipos){
                page_title.active = true;
            }
            else{
                page_title.active = false;
            }
        }

    }



    private go_GameHome():void{
        AudioManager.playSound("UI_click");
        common_getEventTarget().emit(EventEnum_Scene.Go_GameHome);
        
    }

    public update(){
        
    }



}



var _proc_R_scene:Proc_R_scene = null;


export function getProc_R_scene() {

    if(_proc_R_scene === null) {
        _proc_R_scene = new Proc_R_scene();
        console.log('new Proc_R_scene');
    }
    return _proc_R_scene;
} 

