import { _decorator, Component, Node,find,instantiate, EventTouch, math, Label ,log} from 'cc';
const { ccclass, property } = _decorator;
import { common_getEventTarget ,EventEnum_Scene} from './Common';

@ccclass('gtool_start_time')
export class gtool_start_time extends Component {
    
   
    @property({ type: Number })
    public last_time_sec:number = 3;

    @property({ type: Label })
    public time_lable:Label = null;

    private _now_last_time:number = 0;
    private last_timestamp:number = 0;//FPS
    public blTimesUP:boolean = false;

    private strPath:string[] = ["_1","_2","_3"];
    private numberNode:Node[] = [];

    
    start() {
        this.switchShow(true);
    }
    
    public switchShow(_sw:boolean){
        if(_sw){
            this.numberNode = [];
            for(let i=0;i<this.strPath.length;i++){
                let node:Node = this.node.getChildByPath(this.strPath[i]);
                if(node){
                    this.numberNode.push(node);
                    node.active = false;
                }
            }

            this._now_last_time=Date.now()+this.last_time_sec*1000;
            this.update_time_lable();
            this.blTimesUP = false;
            this.node.active = true;
            if(this.time_lable){
                this.time_lable.node.active = false;
            }
        }
        else{
            this._now_last_time = Date.now();
        }

    }

    update_time_lable(){
        if(this.time_lable == null){
            return;
        }
        if(this.numberNode.length == 0) return;

        let now_time = this._now_last_time-Date.now();
        if(now_time<=0){
            now_time = 0;
        }
        let iSec:number = Math.floor(now_time/1000);
        if(iSec >=0 && iSec<3){
            if(this.numberNode[iSec].active == false){
                for(let i=0;i<this.numberNode.length;i++){
                    this.numberNode[i].active = false;
                }
                this.numberNode[iSec].active = true;
            }
        }
        //let strtime = "..."+Math.floor(now_time/1000)+"...";
        //this.time_lable.string = strtime;

    }

    update(deltaTime: number) {
        //
        let elapsed_time = Date.now() - this.last_timestamp;
        
        if(elapsed_time < 33){
            return;
        }
        this.last_timestamp = Date.now();    
        this.update_time_lable();
        if(this._now_last_time-Date.now()<=0){
            //this.node.active = false;
            this.blTimesUP = true;
        }
        
    }
}

