import { _decorator, Component, Node,find,instantiate, EventTouch, math } from 'cc';
const { ccclass, property } = _decorator;
import { common_getEventTarget ,EventEnum_Scene} from './Common';

@ccclass('gameA_SquareT')
export class gameA_SquareT extends Component {
    
   
    @property({ type: Number })
    public SquareNumber:Number = 0;

    @property({ type: Node })
    public SquareNode:Node = null;
    
    start() {
        this.node.on(Node.EventType.TOUCH_START, this.onTouchStart, this);
    }
       
    onTouchStart(){
        common_getEventTarget().emit(EventEnum_Scene.GameA_event_SquareTouch,this.SquareNode,this.SquareNumber);
    }

    update(deltaTime: number) {
      
          
        
    }
}

