import { _decorator, Component, Node,find,instantiate, EventTouch, math } from 'cc';
const { ccclass, property } = _decorator;
import { common_getEventTarget ,EventEnum_Scene,getST} from './Common';
import { AudioManager } from './AudioManager';

@ccclass('ground_t')
export class ground_t extends Component {
    
    wait_time:number = 0;
    spNodes:Node[] = [];
    magic_circle:Node = null;
    str_sp_path:string[] = [
        "Canvas/ground/btn_event_Mask/ground/01/spriteFrame",
        "Canvas/ground/btn_event_Mask/ground/01/spriteFrame-001",
        "Canvas/ground/btn_event_Mask/ground/01/spriteFrame-002",
        "Canvas/ground/btn_event_Mask/ground/01/spriteFrame-003",
        "Canvas/ground/btn_event_Mask/ground/01/spriteFrame-004"    
    ];
    str_sp_name:string[] = [
        "spriteFrame",
        "spriteFrame-001",
        "spriteFrame-002",
        "spriteFrame-003",
        "spriteFrame-004"    
    ];
    s02:Node = null;
    sp_step:number = 0; //1-12
    base_spNowY:number = 0; //
    base_groundY:number = 0;//
    add_eulerAnglesY:number = 0;//
 

    newPath:number[][] = [
        
        [3,2,4,1,0],
        [2,3,4,1,0],
        [2,3,1,4,0],
        [1,2,3,0,4],
        [1,2,3,0,4],
        [1,2,0,4,3],
        [0,1,4,3,2],
        [0,1,4,3,2],
        [0,4,1,3,2],
        [4,0,3,1,2],
        [4,3,0,2,1],
        [3,2,4,1,0],
    ];
    //spBaseAngle:number[] = [70,350,260,200,140];
    spBaseAngle:number[] = [0,72,144,216,288];

    //base 105(1)   0=175 1=94  2=10  3=303 4=245
    //105+70=175    0=70  1=340 2=265 3=198 4=140  
    //base 175   175 103  31 319 247   
    itouchMove:number = 0;
    blTouch:boolean = false;
    blMove:boolean = false;
    itouch_bk_add_eulerAnglesY:number = 0;
    to_endY:number = -1;
    to_addY:number = 0;
    lastMoved:number = 0;
   
    private blCanTouch:boolean = true;

 

    
    start() {
        console.log(this.node.name+" start");
      //  console.log(this.node.eulerAngles);
        for(let i = 0;i < this.str_sp_path.length;i++){
            this.spNodes.push(find(this.str_sp_path[i]));
        }
        this.magic_circle = instantiate(find("Canvas/ground/btn_event_Mask/ground/01/magic_circle"));
        let s01:Node = find("Canvas/ground/btn_event_Mask/ground/01");
        s01.active = false;

        this.s02 = find("Canvas/ground/btn_event_Mask/ground/02");
        this.base_spNowY = this.spNodes[0].eulerAngles.y;
        this.base_groundY = this.node.eulerAngles.y;

        
        this.spNodes[0].scale

        //-------------------------
        let _pos = getST().select_Game;
        this.add_eulerAnglesY = 360-this.spBaseAngle[_pos-1];
        if(this.add_eulerAnglesY == 360)
            this.add_eulerAnglesY = 0;
        common_getEventTarget().emit(EventEnum_Scene.Ground_Event_UpdateGroup,_pos);
        //-------------------------------------
      

        this.sp_update(this.add_eulerAnglesY);
        let touch_node = find("Canvas/ground/btn_event_Mask");
        touch_node.on(Node.EventType.TOUCH_MOVE, this.onEventMove, this);
        touch_node.on(Node.EventType.TOUCH_START, this.onEventStart, this);
        touch_node.on(Node.EventType.TOUCH_END, this.onEventEnd, this);
        touch_node.on(Node.EventType.TOUCH_CANCEL, this.onEventEnd, this);


    this.enableTouch();
        
    }
    onEventStart(event:EventTouch){
        if(this.blCanTouch == false) return;

        this.blTouch = true;
        this.itouch_bk_add_eulerAnglesY = this.add_eulerAnglesY;
    }

    onBookEventEnd(event:EventTouch){
        if(this.blCanTouch == false) return;

        if(this.blMove==true){
            return;
        }
        AudioManager.playSound("UI_click");
        let pos = this.str_sp_name.indexOf(event.target.name)+1;
        
        console.log("onBookEventEnd "+pos,event.target.name);

        this.to_endY = Math.floor(360-this.spBaseAngle[pos-1]);
        if(this.to_endY  >= 360){
            this.to_endY -= 360;
        }
        console.log(pos,this.to_endY);
        this.add_eulerAnglesY = Math.floor(this.add_eulerAnglesY);
        if(this.to_endY == this.add_eulerAnglesY){
            return;
        }
        if(this.to_endY > this.add_eulerAnglesY){
            this.to_addY = 1;
        }else{
            this.to_addY = -1;
        }
        
        
        
    }

    onEventEnd(event:EventTouch){
        if(this.blCanTouch == false) return;
        
        this.blTouch = false;
        this.blMove = false;
        let moveX = event.touch.getLocation().x - event.touch.getStartLocation().x;
        if(moveX < 3 && moveX > -3){
            this.add_eulerAnglesY = this.itouch_bk_add_eulerAnglesY;
            return;
        }
        
        let iPos:number = -1;
        let iPosM:number = -1;
        let iValue:number = -1;
        let iValue2:number = -1;
        if(this.to_addY > 0){
            iValue = 360;
            iValue2 = 360;
            for(let i = 0;i < this.spBaseAngle.length;i++){
                let tmpValue = this.spBaseAngle[i] - this.add_eulerAnglesY;
                if(tmpValue>=0 && tmpValue < iValue){
                    iValue = tmpValue;
                    iPos = i;
                }
                if(this.spBaseAngle[i] < iValue2){
                    iValue2 = this.spBaseAngle[i];
                    iPosM = i;
                }
            } 
            if(iPos == -1){
                iPos = iPosM;
            }
        }
        else{
            iValue = 360;
            iValue2 = 0;
            for(let i = 0;i < this.spBaseAngle.length;i++){
                let tmpValue = this.add_eulerAnglesY - this.spBaseAngle[i];
                if(tmpValue>=0 && tmpValue < iValue){
                    iValue = tmpValue;
                    iPos = i;
                }
                if(this.spBaseAngle[i] > iValue2){
                    iValue2 = this.spBaseAngle[i];
                    iPosM = i;
                }
            } 
            if(iPos == -1){
                iPos = iPosM;
            }  
        }
        this.to_endY = Math.floor(this.spBaseAngle[iPos]);
        this.add_eulerAnglesY = Math.floor(this.add_eulerAnglesY);

        console.log("iPos:"+iPos);
        console.log("to_endY:"+this.to_endY);
        console.log("add_eulerAnglesY:"+this.add_eulerAnglesY);
        console.log("to_addY:"+this.to_addY);
        
        
    }

    onEventMove(event:EventTouch){
        if(this.blCanTouch == false) return;

        let moveX = event.touch.getLocation().x - event.touch.getStartLocation().x;
        this.itouchMove = moveX;
        if(this.blMove == false){
            if(moveX > 3 || moveX < -3){
                this.blMove = true;
            }
        }
        this.add_eulerAnglesY = this.itouch_bk_add_eulerAnglesY + this.itouchMove/2;
        if(this.add_eulerAnglesY >= 360){
            this.add_eulerAnglesY-=360;
        }
        else if(this.add_eulerAnglesY < 0){
            this.add_eulerAnglesY+=360;
        }
        if((event.touch.getLocation().x-this.lastMoved) > 0){
            this.to_addY = 1;
        }
        else{
            this.to_addY = -1;
        }
        this.lastMoved = event.touch.getLocation().x;
    }

    public enableTouch(){
        this.blCanTouch = true;
    }

    public disableTouch(){
        this.blCanTouch = false;
    }

    //
    getScaleByeulerAnglesY(eulerAnglesY:number):number{
        let iScale:number = 0;//1-0.6
        let base = 0.6;

        if(eulerAnglesY >= 360){
            eulerAnglesY -= 360;
        }
        else if(eulerAnglesY < 0){
            eulerAnglesY += 360;
        }

        if(eulerAnglesY > 180){
            eulerAnglesY = 360 - eulerAnglesY;
        }
        iScale = base + (1-base)*((180-eulerAnglesY)/180);

        return iScale;
    }

    sp_update(eulerAnglesY:number){
        let step:number = Math.floor(eulerAnglesY/30)+1;
        if(step != this.sp_step){
            let oldstep = this.sp_step;
            this.sp_step = step;

            //console.log("step:"+step,this.newPath[step-1]);
            
           
            if(this.s02.children.length == 0){

                for(let i = 0;i < this.newPath[step-1].length;i++){
                    if(i==0){
                        this.magic_circle.active = true;
                        this.s02.addChild(this.magic_circle);
                    }

                    let newNode:Node = instantiate(this.spNodes[this.newPath[step-1][i]]);
                    newNode.on(Node.EventType.TOUCH_END, this.onBookEventEnd, this);
                    newNode.active = true;
                    this.s02.addChild(newNode);
                }
                
            }
            else{
                let oldNode:Node[] = [];
              
                this.s02.removeChild(this.magic_circle);
                for(let i = 0;i < 5;i++){
                    for(let j = 0;j < 5;j++){
                        if(this.newPath[oldstep-1][j] == i){
                            oldNode.push(this.s02.children[j]);
                            break;
                        }
                    }
                }
                
                
                this.s02.removeAllChildren();
        
                for(let i = 0;i < 5;i++){
                    if(i==0){
                        this.s02.addChild(this.magic_circle);
                    }

                    this.s02.addChild(oldNode[this.newPath[step-1][i]]);
                    
                }
            }
            
        }
    }

    groundUpdate(){
        let rot = this.node.eulerAngles.clone();
        rot.y = this.base_groundY + this.add_eulerAnglesY;
        if(rot.y >= 360){
            rot.y -= 360;
        }
        this.node.setRotationFromEuler(rot); 
        
        let new_spNowY = this.base_spNowY - this.add_eulerAnglesY;
        if(new_spNowY < 0){
            new_spNowY += 360;
        }
        this.sp_update(this.add_eulerAnglesY);
        
        let nodes:Node[] = [];
        for(let i = 0;i < this.s02.children.length;i++){
            if(this.s02.children[i]!=this.magic_circle){
                nodes.push(this.s02.children[i]);
            }
        }
       // console.log("groundUpdate ",this.newPath[this.sp_step-1],this.add_eulerAnglesY);
        for(let i = 0;i < nodes.length;i++){
            let rot = nodes[i].eulerAngles.clone();
            rot.y = new_spNowY;
            nodes[i].setRotationFromEuler(rot);
           
            //------------------------------------------
            let pos = this.newPath[this.sp_step-1][i];
            let newAngles = this.spBaseAngle[pos] + this.add_eulerAnglesY;
            let newScale = this.getScaleByeulerAnglesY(newAngles)*0.004;
            let nowScale = nodes[i].scale.clone();
           //console.log(newAngles,newScale);
            nowScale.x = newScale;
            nowScale.y = newScale;
            nodes[i].setScale(nowScale);
            //------------------------------------------------
        }    
    }


    

    update(deltaTime: number) {
        this.wait_time += deltaTime*1000;
        
        if(this.wait_time >= 33){
            this.wait_time = 0;
            
            if(this.blTouch == false){
            
                if(this.to_endY != -1){

                    if(this.add_eulerAnglesY == this.to_endY){
                        this.to_endY = -1;
                        let ang:number = 360-this.add_eulerAnglesY;
                        if(ang >= 360){
                            ang -= 360;
                        }
                        let _pos = this.spBaseAngle.indexOf(ang);
                        common_getEventTarget().emit(EventEnum_Scene.Ground_Event_UpdateGroup,_pos+1);
                    }
                   
                    this.add_eulerAnglesY += this.to_addY*3;
                    let neerY = this.to_endY - this.add_eulerAnglesY;
                    if(neerY < 3 && neerY > -3){
                        this.add_eulerAnglesY = this.to_endY;
                    }

                    if(this.add_eulerAnglesY >= 360){
                        this.add_eulerAnglesY-=360;
                    }
                    else if(this.add_eulerAnglesY < 0){
                        this.add_eulerAnglesY+=360;
                    } 
                }
            }

            

            this.groundUpdate();
          
        }  
    }
}

