import { _decorator, Component, Color, Vec2,Sprite } from 'cc';
const { ccclass, property } = _decorator;
import { common_getEventTarget ,EventEnum_Scene} from './Common';
import { SpriteFrameManager } from './SpriteFrameManager';
import { gameD_ColorButton } from './gameD_ColorButton';

@ccclass('gameD_DiamondBox')
export class gameD_DiamondBox extends Component {
    
    @property({ type: gameD_ColorButton })
    public right_btn:gameD_ColorButton = null;

    @property({ type: gameD_ColorButton })
    public left_btn:gameD_ColorButton = null;

    public iBoxID:number = 0;

    //
    private iRL:number = 0;//0=right 1=left
    private iUB:number = 0;//0=UP 1=DOWN

    private Postion_Right_Up:Vec2 = new Vec2(120,440);
    private Postion_Right_Down:Vec2 = new Vec2(120,-440);
    private Postion_Left_Up:Vec2 = new Vec2(-120,440);
    private Postion_Left_Down:Vec2 = new Vec2(-120,-440);

    private last_time:number = 0;
    private iColor:number = 0;//0=blue 1=yallow
    public step:number = 0;//0= 1= 2=

    public blMoveFast:boolean = false;//

    private iRight:number = 1;//0= 1=


    start() {
        
    }
    public init(_BoxID:number,_iColor:number,_iRL:number,_iUB:number,_blMoveFast:boolean){
        this.iBoxID = _BoxID;
        this.iColor = _iColor;
        this.iRL = _iRL;
        this.iUB = _iUB;
        this.blMoveFast = _blMoveFast;
        let _x,_y = 0;
        if(_iRL == 0 && _iUB == 0){
            _x = this.Postion_Right_Up.x;
            _y = this.Postion_Right_Up.y;
        }
        else if(_iRL == 0 && _iUB == 1){
            _x = this.Postion_Right_Down.x;
            _y = this.Postion_Right_Down.y;
        }
        else if(_iRL == 1 && _iUB == 0){
            _x = this.Postion_Left_Up.x;
            _y = this.Postion_Left_Up.y;
        }
        else if(_iRL == 1 && _iUB == 1){
            _x = this.Postion_Left_Down.x;
            _y = this.Postion_Left_Down.y;
        }
        this.node.setPosition(_x,_y);
        this.step = 0;

        if(this.iColor == 0){
            this.node.getComponent(Sprite).spriteFrame = SpriteFrameManager.getSpriteFrame("b_box");
        }
        else{
            this.node.getComponent(Sprite).spriteFrame = SpriteFrameManager.getSpriteFrame("y_box");
        }
        this.node.active = true;
    }

    private move(){
        if(this.step != 0) return;

        let speed:number = 10;
        if(this.blMoveFast == true){
            speed = 15;
        }
        let endY = 2;
        let _y:number = this.node.getPosition().y;
        if(this.iUB == 0){
            _y-=speed;
            if(_y<=endY){
                _y = endY;
                this.step = 1;
            }
        }
        else{
           _y+=speed; 
           if(_y>=endY){
                _y = endY;
                this.step = 1;
            }
        }
        this.node.setPosition(this.node.getPosition().x,_y);
        if(this.step == 1){
            this.iRight = 1;//0= 1=
            if(this.iRL == 0){
                if(this.iColor == this.right_btn.iColor){
                    this.iRight = 0;
                }
            }
            else{
                if(this.iColor == this.left_btn.iColor){
                    this.iRight = 0;
                }   
            }
            if(this.iColor == 0){
                if(this.iRight == 0){
                    this.node.getComponent(Sprite).spriteFrame = SpriteFrameManager.getSpriteFrame("b_box_right");
                }
                else{
                    this.node.getComponent(Sprite).spriteFrame = SpriteFrameManager.getSpriteFrame("b_box_err");
                }
            }
            else{
                if(this.iRight == 0){
                    this.node.getComponent(Sprite).spriteFrame = SpriteFrameManager.getSpriteFrame("y_box_right");
                }
                else{
                    this.node.getComponent(Sprite).spriteFrame = SpriteFrameManager.getSpriteFrame("y_box_err");
                }
            }
            common_getEventTarget().emit(EventEnum_Scene.GameD_event_BoxMoveEnd,this.iRight,this.iBoxID);
        }
    }

    

    update(deltaTime: number) {
        if(this.step == 0){
            if(Date.now() - this.last_time >= 33){
                this.move();
                this.last_time = Date.now();
            }
        }
        else if(this.step == 1){
            let ani_step = (Date.now() - this.last_time)/33;
          
            let _scale:number = ani_step*0.06+1;
            if(this.iRight == 0){
                this.node.setScale(_scale,_scale);
            }
            this.node.getComponent(Sprite).color = new Color(255,255,255,255-ani_step*20);

            if(ani_step >= 10){
                this.step = 2;
                this.node.active = false;
                common_getEventTarget().emit(EventEnum_Scene.GameD_event_BoxMoveEnd,2,this.iBoxID);
            }
        }
        
    }
}

