import { find, UITransform,Node,RichText} from 'cc';
import { common_getEventTarget,EventEnum_Scene,common_bind_touch_start } from './Common';
import { AudioManager } from './AudioManager';

class Proc_Info{ 
   

    //
    private btn_goHome_name:string[] = ["Canvas/"];

  
    public init():void{

        //
        for(let i=0;i<this.btn_goHome_name.length;i++){
            common_bind_touch_start(this.btn_goHome_name[i],this.goHome,this);
        }

        //N1
        common_bind_touch_start("Canvas/N_Button/button",function(){
            window.location.href = "http://axis.chanliu.org/";
        }
        ,this);
        //N2
        common_bind_touch_start("Canvas/N_Button/button-001",function(){
            window.location.href = "http://flames.chanliu.org/";
        }
        ,this);
        //N3
        common_bind_touch_start("Canvas/N_Button/button-002",function(){
            window.location.href = "http://cachecache.chanliu.org/";
        }
        ,this);
    }


    private goHome(){
        AudioManager.playSound("UI_click");
        common_getEventTarget().emit(EventEnum_Scene.Go_GameHome);
    }

}



var _proc_info:Proc_Info = null;


export function getProc_Info() {

    if(_proc_info === null) {
        _proc_info = new Proc_Info();
        console.log('new Proc_GameHome');
    }
    return _proc_info;
} 

