import { _decorator, Component, Node,find,instantiate, EventTouch, math, log, Vec2 } from 'cc';
const { ccclass, property } = _decorator;
import { common_getEventTarget ,EventEnum_Scene} from './Common';

@ccclass('gameC_MapButton')
export class gameC_MapButton extends Component {
    
    private blTouchSW:boolean = false;//false: true:
    private path_name:string[] = [];
    private istouch:boolean[] = [];
    private basePostion:Vec2 = new Vec2(100,835);//-187 190
    private basePostion_add:number = 125;

    start() {
        this.blTouchSW = false;

        let path_base:string = "Canvas///_";
       
        for(let i=0;i<16;i++){
            let path = path_base;
            let pname = "_"
            if(i>0){
                path = path_base + "-"+i.toString().padStart(3, '0');
                pname+= "-"+i.toString().padStart(3, '0');
            }
            let node = find(path);
            
            node.on(Node.EventType.TOUCH_START, this.onTouchStart,this);
            node.on(Node.EventType.TOUCH_MOVE, this.onTouchMove,this);
            node.on(Node.EventType.TOUCH_END, this.onTouchEnd,this);
            node.on(Node.EventType.TOUCH_CANCEL, this.onTouchEnd,this);

            this.path_name.push(pname);
            this.istouch.push(false);
            if(i == 0){
                console.log(node,node.getWorldPosition());
            }
        }

    }
    //
    public touchSwitch(sw:boolean){
        this.blTouchSW = sw;
    }

    public resetTouch(){
        for(let i=0;i<16;i++){
            this.istouch[i] = false;
        }   
    }

    private check_Link(_index:number):boolean{
        let _cnt_touched:number = 0;
        for(let i=0;i<16;i++){
            if(this.istouch[i]){
                _cnt_touched++;
            }
        }
        if(_cnt_touched==0) return true;
        let _x = _index%4;
        let _y = Math.floor(_index/4);
        let blLink:boolean = false;
        if(_x-1>=0){
            let _index2 = _y*4 + (_x-1);
            if(this.istouch[_index2]){
                blLink = true;
            }
        }
        if(_x+1<=3){
            let _index2 = _y*4 + (_x+1);
            if(this.istouch[_index2]){
                blLink = true;
            }
        }
        if(_y-1>=0){
            let _index2 = (_y-1)*4 + _x;
            if(this.istouch[_index2]){
                blLink = true;
            }
        }
        if(_y+1<=3){
            let _index2 = (_y+1)*4 + _x;
            if(this.istouch[_index2]){
                blLink = true;
            }
        }
        if(blLink==false) return false;

        return true;
    }
    onTouchMove(event: EventTouch) {
        if(this.blTouchSW==false) return;
        let _chgBase_x = event.touch.getUILocation().x-this.basePostion.x;
        let _chgBase_y = this.basePostion.y-event.touch.getUILocation().y;
        let _x = Math.floor(_chgBase_x/this.basePostion_add);
        let _y = Math.floor(_chgBase_y/this.basePostion_add);
        if(_x<0) _x = 0;
        if(_x>3) _x = 3;
        if(_y<0) _y = 0;
        if(_y>3) _y = 3;
      
        let index = _y*4 + _x;
        
    
        //log(index,_x,_y,event.type,event.touch.getUILocation().x,event.touch.getUILocation().y);

        if(this.istouch[index] == true) return;

        if(this.check_Link(index)==false) return;
        
        this.istouch[index] = true;

        common_getEventTarget().emit(EventEnum_Scene.GameC_event_MapButton,index);

      
      
    }
       
    onTouchStart(event: EventTouch) {
        if(this.blTouchSW==false) return;
      
        let index = this.path_name.indexOf(event.target.name);
      
       // log(index,event.type,event.getLocationX(),event.getLocationY());
      
        if(this.istouch[index] == true) return;

        if(this.check_Link(index)==false) return;
        
        this.istouch[index] = true;
        
        common_getEventTarget().emit(EventEnum_Scene.GameC_event_MapButton,index);
    }

    onTouchEnd(event: EventTouch) {
        if(this.blTouchSW==false) return;

        common_getEventTarget().emit(EventEnum_Scene.GameC_event_MapButton,255);

    }

    update(deltaTime: number) {
        
    }
}

