import { find, UITransform,Node,RichText, Sprite, Label} from 'cc';
import { common_getEventTarget,EventEnum_Scene,common_bind_touch_start,getST } from './Common';
import { AudioManager } from './AudioManager';
import { SpriteFrameManager } from './SpriteFrameManager';
import { ground_t } from './ground_t';

class Proc_GameHome{ 
    public game_scene_name:string[][]=[
        ["CognitionScene"],
        ["MathScene"],
        ["MemoryScene"],
        ["Rankings"],
        ["AttentionScene"]
    ];
    public game_sprite_name:string[][]=[
        ["gh_mis_1_1"],
        ["gh_mis_2_1"],
        ["gh_mis_3_1"],
        ["gh_mis_4_1"],
        ["gh_mis_5_1"]
    ];
    public game_book_sprite_name:string[][]=[
        ["gh_mis_book_1"],
        ["gh_mis_book_2"],
        ["gh_mis_book_3"],
        ["gh_mis_book_4"],
        ["gh_mis_book_5"]
    ];

    private gameSprite:Sprite = null;
    private gameBookSprite:Sprite = null;

    //
    private btn_goLogin_name:string[] = ["Canvas/leave_btn"];


    //Info
    private btn_goInfo_name:string = "Canvas/info/button";

   
    public gh_group:number = 1;//1-5
    public gh_subpage:number=1;//
   
    
    public init():void{
        common_getEventTarget().on(EventEnum_Scene.Ground_Event_UpdateGroup, this.ground_event_UpdateGroup,this);

        //
         for(let i=0;i<this.btn_goLogin_name.length;i++){
             common_bind_touch_start(this.btn_goLogin_name[i],this.gh_show_exit,this);
         }
        common_bind_touch_start("Canvas/Exit_game/yes",this.goLogin_Y,this);
        common_bind_touch_start("Canvas/Exit_game/no",this.goLogin_N,this);
        find("Canvas/Exit_game").active = false;


        common_bind_touch_start(this.btn_goInfo_name,this.goInfo,this);

        this.gameSprite = find("Canvas/level_book/gameSprite").getComponent(Sprite);
        this.gameSprite.spriteFrame = SpriteFrameManager.getSpriteFrame(this.game_sprite_name[this.gh_group-1][this.gh_subpage-1]);

        this.gameBookSprite = find("Canvas/level_book/BG_book/gameSprite_book").getComponent(Sprite);
        this.gameBookSprite.spriteFrame = SpriteFrameManager.getSpriteFrame(this.game_book_sprite_name[this.gh_group-1][this.gh_subpage-1]);

        common_bind_touch_start("Canvas/level_book/gameSprite/btn_event_Mask",this.goGame,this);

        if(getST().get_bl_open_test_debug()){
            let debug_btn:Node = find("Canvas//debug_btn");
            debug_btn.active = true;

            debug_btn.on(Node.EventType.TOUCH_START, function(){
                if(getST().switchTestMode()){
                    find("Canvas//debug_btn/Label_sw").getComponent(Label).string = "Open";
                }else{
                    find("Canvas//debug_btn/Label_sw").getComponent(Label).string = "Close";
                }      
            }, this);

            if(getST().getIsInTestMode()){
                find("Canvas//debug_btn/Label_sw").getComponent(Label).string = "Open";
            }
            else{
                find("Canvas//debug_btn/Label_sw").getComponent(Label).string = "Close";
            }
        }
        //
        find("Canvas/Rank/button").on(Node.EventType.TOUCH_START, function(){
            AudioManager.playSound("UI_click");
            this.gh_subpage = 1;
            this.gh_group = 4;
            getST().select_Game=4;
            this.goGameUnlock();
        }, this);

        AudioManager.playMusic("bgm01");
    }

    private goInfo(){
        AudioManager.playSound("UI_click");
        common_getEventTarget().emit(EventEnum_Scene.Go_Info);
    }

    public gh_show_exit(){
      
        let node:Node = find("Canvas/Exit_game");
        node.active = true;
        find("Canvas/ground/btn_event_Mask/ground").getComponent(ground_t).disableTouch();
    }

    private ground_event_UpdateGroup(_pos:number){
        console.log("ground_event_GoGame:"+_pos);

       
        this.gh_group = _pos;
        //
        getST().select_Game=this.gh_group;

        this.gh_subpage = 1;
        console.log("gh_group:"+this.gh_group);
        
        this.gameSprite.spriteFrame = SpriteFrameManager.getSpriteFrame(this.game_sprite_name[this.gh_group-1][this.gh_subpage-1]);

        this.gameBookSprite.spriteFrame = SpriteFrameManager.getSpriteFrame(this.game_book_sprite_name[this.gh_group-1][this.gh_subpage-1]);

        
    }

    private goGame(){
        AudioManager.playSound("UI_click");
        if(getST().select_Game==4){
            return;
        }

        common_getEventTarget().emit(EventEnum_Scene.Go_Game);
    }

    private goGameUnlock(){
        AudioManager.playSound("UI_click");
        
        common_getEventTarget().emit(EventEnum_Scene.Go_Game);
    }

    private goLogin_Y(){

        AudioManager.playSound("UI_click");
        window.location.href = "../index.html";
    }

    public goLogin_N(){
        AudioManager.playSound("UI_click");
        let node:Node = find("Canvas/Exit_game");
        node.active = false;
        find("Canvas/ground/btn_event_Mask/ground").getComponent(ground_t).enableTouch();
    }
    

    public getGameSceneName():string{
        
        return this.game_scene_name[this.gh_group-1][this.gh_subpage-1];
    }
    
}



var _proc_gameHome:Proc_GameHome = null;


export function getProc_GameHome() {

    if(_proc_gameHome === null) {
        _proc_gameHome = new Proc_GameHome();
        console.log('new Proc_GameHome');
    }
    return _proc_gameHome;
} 

