import { find, UITransform,Node,RichText, Sprite, Color, log, EventTouch,Label} from 'cc';
import { common_getEventTarget,EventEnum_Scene,common_bind_touch_start,getST,CState,CFSM } from './Common';
import { AudioManager } from './AudioManager';
import { SpriteFrameManager } from './SpriteFrameManager';
import { gtool_start_time } from './gtool_start_time';
import { gameB_MathButton } from './gameB_MathButton';



//
enum B_scene_state  { 
    st_none=100,    //
    st_init,        // 
    st_tutorial,    //
    st_into_animation, //
    st_nextMission,//
    st_play_input, //
    st_show_result, // //
    st_show_mission_win, //
    st_show_mission_err, //
    st_show_score, //
    st_timeup_animation, //
    st_end, //
}

class Proc_B_scene{ 

    
    private last_timestamp:number = 0;//FPS
    private cFSM:CFSM = new CFSM();

    private md:any = null;
    private mission:number = 0;//
    private right_mission:number = 0;//
    private error_mission:number = 0;//
    private ComboCount:number = 0;//
    private last_is_right:boolean = false;//

    private play_last_time:number = 0;//

    private node_StartTime:Node = null;//
    private node_TimesUp:Node = null;//
    private node_score_panle:Node = null;//

    private question_num:number[] = [];//10  
    private questionSet_num:number[] = [];
    private questionSet_rate:number[] = [];

    private Questions_node:Node[] = [];//

    private now_answer:number = 0;//
  
    private strTutorialPath:string[] = [
        "Canvas/tutorial/_01",
        "Canvas/tutorial/_02",
        "Canvas/tutorial/_03"
    ];
    private blPlay10TimesSound:boolean = false;
    
    public init(){

        //
        this.cFSM = new CFSM();
        this.add_FSM_state();

        this.cFSM.set_Next_state(B_scene_state.st_init);
        this.last_timestamp = Date.now();


        this.md = getST().get_mission_data();
        this.mission = 0;
        this.right_mission = 0;
        this.ComboCount = 0;
        this.last_is_right = false;
        

        common_getEventTarget().on(EventEnum_Scene.GameB_event_MathButton, this.gameB_event_MathButton, this);
        find("Canvas/").once(Node.EventType.TOUCH_START, function(){
            AudioManager.playSound("UI_click");
            common_getEventTarget().emit(EventEnum_Scene.Go_GameHome);
        }, this);

        find("Canvas/tutorial").active = false;
        this.node_StartTime = find("Canvas/start");
        this.node_StartTime.active = false;
        this.node_TimesUp = find("Canvas/timesup");
        this.node_TimesUp.active = false;
        this.node_score_panle = find("Canvas/score_panle");
        this.node_score_panle.active = false;

        this.Questions_node = [];
        this.Questions_node.push(find("Canvas/Questions/Questions_001"));
        this.Questions_node.push(find("Canvas/Questions/Questions_002"));
        this.Questions_node.push(find("Canvas/Questions/Questions_003"));


        //
        let rateCnt:number = 0;
        for(var i=0;i<this.md.QuestionSet.QuestionType.length;i++){
            this.questionSet_num.push(this.md.QuestionSet.QuestionType[i]);
            rateCnt+=this.md.QuestionSet.QuestionProbability[i];
            this.questionSet_rate.push(rateCnt);
        }
        
    }
    private add_FSM_state(){
        this.cFSM.add_new_state(B_scene_state.st_init,"st_init",this.st_init_into,this.st_init_update,this);
        this.cFSM.add_new_state(B_scene_state.st_tutorial,"st_tutorial",this.st_tutorial_into,this.st_tutorial_update,this);
        this.cFSM.add_new_state(B_scene_state.st_into_animation,"st_into_animation",this.st_into_animation_into,this.st_into_animation_update,this);
        this.cFSM.add_new_state(B_scene_state.st_nextMission,"st_nextMission",this.st_nextMission_into,this.st_nextMission_update,this);
        this.cFSM.add_new_state(B_scene_state.st_play_input,"st_play_input",this.st_play_input_into,this.st_play_input_update,this);
        this.cFSM.add_new_state(B_scene_state.st_show_result,"st_show_result",this.st_show_result_into,this.st_show_result_update,this);
        this.cFSM.add_new_state(B_scene_state.st_show_mission_win,"st_show_mission_win",this.st_show_mission_win_into,this.st_show_mission_win_update,this);
        this.cFSM.add_new_state(B_scene_state.st_show_mission_err,"st_show_mission_err",this.st_show_mission_err_into,this.st_show_mission_err_update,this);
        this.cFSM.add_new_state(B_scene_state.st_show_score,"st_show_score",this.st_show_score_into,this.st_show_score_update,this);
        this.cFSM.add_new_state(B_scene_state.st_timeup_animation,"st_timeup_animation",this.st_timeup_animation_into,this.st_timeup_animation_update,this);
        this.cFSM.add_new_state(B_scene_state.st_end,"st_end",this.st_end_into,this.st_end_update,this);

    }

    public gameB_event_MathButton(num:number) {
        log("gameB_event_MathButton:"+num);
        this.now_answer+=num;
        if(this.now_answer == this.question_num[0]){
            this.cFSM.set_Next_state(B_scene_state.st_show_mission_win);
        }
        else if(this.now_answer > this.question_num[0]){
            this.cFSM.set_Next_state(B_scene_state.st_show_mission_err);
        }
    }

    private get_next_question():number{
        if(this.question_num.length == 0){
            this.create_10_question();
        }
        let q:number = this.question_num.shift();
        this.create_10_question();
        return q;
    }

    private create_10_question(){
        let need_cnt = 10-this.question_num.length;
        for(let i=0;i<need_cnt;i++){
            //
            while(true){
             
                let rand:number = Math.random()*this.questionSet_rate.at(-1);
             
                let idx:number = 0;
                for(let k=0;i<this.questionSet_rate.length;k++){
                    if(rand<=this.questionSet_rate[k]){
                        idx = k;
                        break;
                    }
                }

                let q:number = this.questionSet_num[idx];

                let is_same = false;
                for(let j=0;j<this.question_num.length;j++){
                    if(this.question_num[j]==q){
                        is_same = true;
                        break;
                    }
                }
                if(is_same==false){
                    this.question_num.push(q);
                    break;
                }
            }
        }
    }
 



    public st_init_into(){
  
        this.question_num = [];

        this.mission = 0;
        this.right_mission = 0;
        this.error_mission = 0;
        this.ComboCount = 0;
        this.last_is_right = false;
        this.node_score_panle.active = false;
        this.node_TimesUp.active = false;
        this.node_StartTime.active = false;

        for(var i=0;i<this.Questions_node.length;i++){
            this.Questions_node[i].getChildByName("MathText").getComponent(RichText).string = "<color=#000000>--</color>";
        }

        this.cFSM.set_Next_state(B_scene_state.st_tutorial);
        this.blPlay10TimesSound = false;
    }
    
    public st_init_update(){}
    public st_tutorial_into(){
        find("Canvas/tutorial").active = true;
        let btn_start = find("Canvas/tutorial/btn_start");
        let btn_next_page = find("Canvas/tutorial/btn_next_page");
        btn_start.once(Node.EventType.TOUCH_START,
            function(){
                this.cFSM.set_Next_state(B_scene_state.st_into_animation);
                find("Canvas/tutorial").active = false;
                this.node_StartTime.getComponent(gtool_start_time).switchShow(true);
                AudioManager.playSound("countdown01");
            }, this);

        btn_start.active = true;
        btn_next_page.active = true;

        btn_next_page.off(Node.EventType.TOUCH_START);
        btn_next_page.on(Node.EventType.TOUCH_START,
            function(){
                let _page:number = 0;
                for(let i=0;i<this.strTutorialPath.length;i++){
                    if(find(this.strTutorialPath[i]).active == true){
                        _page = i;
                        break;
                    }
                }
                _page++;
                if(_page >= this.strTutorialPath.length){
                    _page = 0;
                }
                
               log("page = "+_page);
                for(let i=0;i<this.strTutorialPath.length;i++){
                    find(this.strTutorialPath[i]).active = false;
                    if(i == _page){
                        find(this.strTutorialPath[i]).active = true;
                    }
                }
            }, this);

        for(let i=0;i<this.strTutorialPath.length;i++){
            find(this.strTutorialPath[i]).active = false;
        }
        find(this.strTutorialPath[0]).active = true;
        
    }
    public st_tutorial_update(){}
    public st_into_animation_into(){
        AudioManager.stopMusic();
    }
    public st_into_animation_update(){
        if(this.node_StartTime.getComponent(gtool_start_time).blTimesUP){
            this.node_StartTime.active = false;
            this.cFSM.set_Next_state(B_scene_state.st_nextMission);
            this.play_last_time = this.md.Time*1000;

            AudioManager.playMusic("bgm02");
        }
    }
    public st_nextMission_into(){
        this.mission++;
        this.now_answer = 0;

        this.get_next_question();
    
        ///
        for(var i=0;i<this.Questions_node.length;i++){
            let rt:RichText = this.Questions_node[i].getChildByName("MathText").getComponent(RichText);
            rt.string = "<color=#000000>"+this.question_num[i]+"</color>";
        }
        this.cFSM.set_Next_state(B_scene_state.st_play_input);
    }
    public st_nextMission_update(){}
    public st_play_input_into(){
        find("Canvas/mathButton").getComponent(gameB_MathButton).touchSwitch(true);
    }
    public st_play_input_update(){}
    public st_show_result_into(){}
    public st_show_result_update(){}
    public st_show_mission_win_into(){
        find("Canvas/mathButton").getComponent(gameB_MathButton).touchSwitch(false);   
        find("Canvas/Answer/ok").active = true;
        this.right_mission++;
        AudioManager.playSound("answer02");
        this.cFSM.get_now_state().wait_time = 500;
        if(this.last_is_right){
            this.ComboCount++;
        }
        this.last_is_right = true;
    }
    public st_show_mission_win_update(){
        find("Canvas/Answer/ok").active = false;
        this.cFSM.set_Next_state(B_scene_state.st_nextMission);    
    }
    public st_show_mission_err_into(){
        find("Canvas/mathButton").getComponent(gameB_MathButton).touchSwitch(false);  
        this.error_mission++;
        AudioManager.playSound("answer01");
        find("Canvas/Answer/err").active = true;
        this.cFSM.get_now_state().wait_time = 500;
        this.last_is_right = false;
    }
    public st_show_mission_err_update(){
        find("Canvas/Answer/err").active = false;
        this.cFSM.set_Next_state(B_scene_state.st_nextMission);  
    }
    public st_show_score_into(){
        this.mission = this.right_mission+this.error_mission;
        getST().setWin(this.mission,this.right_mission,this.ComboCount);
        this.node_score_panle.active = true;
        this.node_TimesUp.active = false;

        this.node_score_panle.getChildByName("again").once(Node.EventType.TOUCH_START,
             function(){
                AudioManager.playSound("UI_click");
                this.cFSM.set_Next_state(B_scene_state.st_init);
             }, 
             this);
        this.node_score_panle.getChildByName("exit").once(Node.EventType.TOUCH_START,
            function(){
                AudioManager.playSound("UI_click");
                this.cFSM.set_Next_state(B_scene_state.st_end);
            }, 
            this);
        
        this.node_score_panle.getChildByName("right_num").getComponent(Label).string = this.right_mission.toString();
        this.node_score_panle.getChildByName("err_num").getComponent(Label).string = this.error_mission.toString();
        this.node_score_panle.getChildByName("rate").getComponent(Label).string = Math.floor(this.right_mission*100/this.mission).toString()+"%";

        this.node_score_panle.getChildByName("time_num").getComponent(Label).string = getST().time_num.toString()+'s';
        this.node_score_panle.getChildByPath("score/right_score").getComponent(RichText).string = "<b><color=#7a4706>"+getST().play_score.toString()+"</color></b>";
        this.node_score_panle.getChildByPath("score/combo_score").getComponent(RichText).string = "<b><color=#7a4706>"+getST().comble_score.toString()+"</color></b>";
        this.node_score_panle.getChildByPath("score/total_score").getComponent(RichText).string = "<b><color=#7a4706>"+getST().play_totle_score.toString()+"</color></b>";
        let bastScore = getST().myRankData[getST().select_Game-1].score;
        this.node_score_panle.getChildByPath("score/total_score-001").getComponent(RichText).string = "<b><color=f8e0b3>"+bastScore.toString()+"</color></b>";
        let strTitlePath:string[] = [ //0    1	  2	  3	  4	
            "txt_objext/title/__",
            "txt_objext/title/__",
            "txt_objext/title/__",
            "",
            "txt_objext/title/__",
        ]
        for(let i=0;i<5;i++){
            if(i == 3)
                continue;
            if(i == (getST().select_Game-1)){
                this.node_score_panle.getChildByPath(strTitlePath[i]).active = true;
            }
            else{
                this.node_score_panle.getChildByPath(strTitlePath[i]).active = false;
            }

        }
        AudioManager.playSound("result01");
    }
    public st_show_score_update(){
        let step:number = Math.floor(this.cFSM.get_now_state().elapsed_time/33);
        if(step <= 25){
            let rate:number =  this.right_mission/this.mission;
            let rateP:number = rate*(step/25);
            rateP = (Math.round(rateP * 100) / 100)*-1;
            find("Canvas/score_panle/_").getComponent(Sprite).fillRange = rateP;
        }
    }
    public st_timeup_animation_into(){
        find("Canvas/mathButton").getComponent(gameB_MathButton).touchSwitch(false);  
        this.node_TimesUp.getComponent(gtool_start_time).switchShow(true);
        AudioManager.stopMusic();
    }
    public st_timeup_animation_update(){
        if(this.node_TimesUp.getComponent(gtool_start_time).blTimesUP){
            this.cFSM.set_Next_state(B_scene_state.st_show_score);
        }
    }
    public st_end_into(){
        common_getEventTarget().emit(EventEnum_Scene.Go_GameHome);
    }
    public st_end_update(){}

    
    public update(){
        //
        let elapsed_time = Date.now() - this.last_timestamp;
        
        if(elapsed_time < 33){
            return;
        }
        this.last_timestamp = Date.now();
        this.cFSM.update();

        
        if(this.cFSM.get_now_state().state == B_scene_state.st_play_input){
            //------------------------
            if(getST().getIsInTestMode()){
                elapsed_time = 0;   
            }
            //---------------------------------
            this.play_last_time = this.play_last_time-elapsed_time;
            if(this.play_last_time < 0){
                this.play_last_time = 0;
            }
            let _sec = Math.floor(this.play_last_time/1000).toString().padStart(2, '0');
            let _msec = Math.floor(this.play_last_time%1000/10).toString().padStart(2, '0');
        
            find("Canvas/time/TimeText_sec").getComponent(RichText).string = "<color=#ffffff>"+_sec+":"+ "</color>";
            find("Canvas/time/TimeText_msec").getComponent(RichText).string = "<color=#ffffff>"+_msec+ "</color>";
            if(this.play_last_time<=0){
                this.cFSM.set_Next_state(B_scene_state.st_timeup_animation);
            }
            if(this.blPlay10TimesSound==false){
                if(this.play_last_time <= 3000){
                    this.blPlay10TimesSound = true;
                    AudioManager.playSound("countdown01");
                }
            }
        }
        
    }
    
}


var _proc_B_scene:Proc_B_scene = null;


export function getProc_B_scene() {

    if(_proc_B_scene === null) {
        _proc_B_scene = new Proc_B_scene();
        console.log('new Proc_B_scene');
    }
    return _proc_B_scene;
} 

